<template>
  <div id="app">
    <app-navigation></app-navigation>
    <div class="content">
      <router-view></router-view>
    </div>
    
    <footer class="mdl-mini-footer">
      <div class="mdl-mini-footer__left-section">
        <div class="mdl-logo">UniOtaku Shoutbox Stats</div>
        <ul class="mdl-mini-footer__link-list">
          <li><a href="https://tracker.uniotaku.com/">Tracker</a></li>
          <li><a href="https://github.com/qgustavor/uss">GitHub</a></li>
        </ul>
      </div>
    </footer>
  </div>
</template>

<script>
import store from 'store'
import { mapActions, mapGetters } from 'vuex'
import appNavigation from './components/Navigation.vue'
export default {
  store,
  components: {appNavigation},
  computed: mapGetters([
    'categories'
  ]),
  methods: {
    ...mapActions([
      'increment'
    ]),
    onLangClick (lang) {
      this.setLang(lang) // mixin method
      this.increment() // store action
    }
  }
}
</script>

<style lang="stylus">
html, body
  min-height: 100%
  background: #46597A
  background-image: radial-gradient(ellipse farthest-corner at 66% 100%, #3F265F, #3F5F8C, #46597A)
  color: white
</style>

<style scoped lang="stylus">    
.content
  min-height: 100vh
</style>



