<template>
  <canvas width="100" :height="heightRatio || 33"></canvas>
</template>

<script>
import Chart from 'chart.js'

export default {
  data () {
    return {
      chart: null
    }
  },
  props: [
    'data',
    'options',
    'heightRatio'
  ],
  mounted () {
    if (this.chart) this.chart.destroy()
    this.chart = new Chart(this.$el, {
      type: 'bar',
      data: this.data,
      options: this.options || {}
    })
  },
  watch: {
    data () {
      const newDatasets = this.data.datasets
      const len = newDatasets.length
      for (let i = 0; i < len; i++) {
        this.chart.data.datasets[i].data = newDatasets[i].data
      }
      this.chart.data.datasets.splice(len)
      this.chart.data.labels = this.data.labels
      this.chart.update()
    },
    options () {
      Object.assign(this.chart.options, this.options)
      this.chart.update()
    }
  },
  destroyed () {
    this.chart.destroy()
  }
}
</script>


