import VueRouter from 'vue-router'
import routes from 'src/routes'

const base = typeof window === 'undefined' ? '/' : (new window.URL('../../', document.scripts[0].src).pathname || '/')

export default {
  router: null,
  install (Vue) {
    const router = new VueRouter({
      routes,
      base,
      mode: 'history'
    })

    Vue.mixin({
      beforeCreate () {
        if (this.$root === this) {
          this.$options.router = router
        }
      }
    })

    Vue.use(VueRouter)
  }
}



