<template>
  <div>
    <div class="rank--top-scores" v-if="!disableTop">
      <div v-for="(item, index) in this.data.slice(0, 3)" :key="item[0]" class="rank-item__top">
        <div class="rank-title">{{ index + 1 }}: {{ item[0] }}</div>
        <div class="rank-avatar" :style="{backgroundImage: 'url(' + getAvatar(item[0]) + ')'}"></div>
        <div class="rank-score">{{ item[1].toLocaleString() }}</div>
      </div>
    </div>
    <table class="mdl-data-table mdl-js-data-table rank-table">
      <thead><tr>
        <th>Rank</th>
        <th class="mdl-data-table__cell--non-numeric">{{ header ? header[0] : 'Usurio' }}</th>
        <th>{{ header ? header[1] : 'Mensagens' }}</th>
      </tr></thead>
      <transition-group name="rank" tag="tbody">
        <tr v-for="(item, index) in this.data.slice(disableTop ? 0 : 3)" :key="item[0]" class="rank-item">
          <td>{{ index + 1 + (disableTop ? 0 : 3) }}</td>
          <td class="mdl-data-table__cell--non-numeric">{{ item[0] }}</td>
          <td>{{ item[1].toLocaleString() }}</td>
        </tr>
      </transition-group>
    </table>
  </div>
</template>

<script>
import avatars from '../assets/reports/avatars.json'

export default {
  props: [
    'data',
    'header',
    'disableTop'
  ],
  methods: {
    getAvatar (username) {
      const matchingUser = avatars.find(e => e[0] === username)
      return matchingUser ? matchingUser[1].replace('http://i.imgur.com/', 'https://i.imgur.com/') : 'https://i.imgur.com/nTQMNMa.png'
    }
  }
}
</script>

<style lang="stylus" scoped>
.rank-table
  width: 100%
  background: rgba(0, 0, 0, 0.1)
  font-size: 0.5em
  table-layout: fixed;
.rank-table th
  color: #fff
  font-size: 1em
.rank-table tbody tr:hover
  background: rgba(0, 0, 0, 0.2)

.rank-item {
  transition: all 1s;
  display: table-row;
  margin-right: 10px;
}
.rank-enter, .rank-leave-active {
  opacity: 0;
}
.rank-leave-active {
  position: absolute;
}
.rank--top-scores
  display: flex
  font-size: 0.5em
  margin-bottom: 0.5em

.rank-item__top
  border: 1px solid rgba(0, 0, 0, 0.3)
  width: 100%
  text-align: center
  background: rgba(0, 0, 0, 0.1)
  
@media screen and (min-width: 480px)
  .rank--top-scores, .rank-table
    font-size: 0.6em
@media screen and (min-width: 840px)
  .rank--top-scores, .rank-table
    font-size: 0.7em
    
.rank-avatar
  width: 100%
  padding-top: 100%
  background-size: cover
  background-position: 50% 10%
  background-color: white
</style>


