<template>
  <div class="navbar-wrapper">
    <div class="navbar" @mouseleave.self="setYear(null)">
      <router-link to="/"><icon class="logo" name="logo"></icon></router-link>
      <div class="navbar__subnav">
        <div class="navbar__year">
          <router-link to="/" class="mdl-button white-btn">Rank Geral</router-link>
          <span @mouseenter="setYear(year)" v-for="year in years">
            <router-link :to="'/' + year" class="mdl-button white-btn"
            :class="{'mdl-button--raised mdl-button--accent': year === currentYear}">Rank de {{ year }}</router-link>
          </span>
        </div>
        <div class="navbar__month mdl-cell--hide-phone mdl-cell--hide-tablet" :style="{visibility: currentYear ? 'visible' : 'hidden'}">
          <router-link class="mdl-button white-btn" v-for="(month, index) in months"
          :to="'/' + (currentYear || 2016) + '/' + ('00' + (index + 1)).substr(-2)">{{ month }}</router-link>
        </div>
      </div>
    </div>
    <div class="navbar__month navbar__month--mobile mdl-cell--hide-desktop" v-if="$route.params.year">
      <span class="mdl-button white-btn mdl-button--raised mdl-button--primary">{{ $route.params.year }} - escolha um ms:</span>
      <router-link class="mdl-button white-btn" v-for="(month, index) in months"
      :to="'/' + $route.params.year + '/' + ('00' + (index + 1)).substr(-2)">{{ month }}</router-link>
    </div>
  </div>
</template>

<script>
import moment from 'moment'

export default {
  data () {
    return {
      years: [2015, 2016],
      months: moment.months(),
      currentYear: null
    }
  },
  methods: {
    setYear (year) {
      this.currentYear = year
    }
  }
}
</script>

<style lang="stylus" scoped>
.navbar-wrapper
  background: rgba(0, 0, 0, 0.1)
  
.navbar
  max-width: 1280px
  margin: 0 auto
  padding: 0 5em
  
  svg
    width: 12em
    height: 8em
    
.navbar__subnav
  width: 100%
  display: flex
  flex-direction: column
  justify-content: center
  
.navbar__year
  display: flex
  flex-direction: column
  align-items: center
  
@media screen and (min-width: 480px)
  .navbar
    display: flex
  .navbar__year
    flex-direction: row
  .navbar__subnav, .navbar__year
    align-items: flex-end
  
.navbar__month
  display: flex
  overflow: auto
  justify-content: flex-end
  flex-wrap: wrap
  
.navbar__month--mobile
  justify-content: space-around
  
.white-btn
  color: white
  font-size: 1em
  margin: 0.25em
  padding: 0.2em 0.4em
</style>



