import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const state = {
  count: 0
}

const mutations = {
  increment (state) {
    state.count++
  }
}

const actions = {
  increment: ({ commit }) => commit('increment')
}

const getters = {
  categories: ({ categories }) => categories
}

export default new Vuex.Store({
  state,
  mutations,
  actions,
  getters
})



